import axios from "axios";
// import { store } from "@/store/store";
import { getToken } from "./token_handle";
import { urls } from "../custom_configs";


// axiosrequest  

const sendAxiosRequest = (api = '', method = 'get', reqParams = {}, reqObject = {}) => {
  // request
  const baseURL = urls.serverUrl; //  api url

  // axios
  const service = axios.create({
    baseURL: baseURL, // apibase_url
    timeout: 20000 // 
  });

  // request
  service.interceptors.request.use(config => {
    config.headers["Content-Type"] = "application/json";
    if (getToken()) {
      // config.headers["Authorization"] = getToken(); // token 
      console.log(getToken())
      config.headers["AUTHORIZATION"] = getToken();
      config.headers["token-type"] = "c";
    }
    return config;
  },
    error => {
      // Do something with request error
      console.log(error); // for debug
      Promise.reject(error);
    }
  );

  // respone
  service.interceptors.response.use(response => {
    /**
     * code1000 
     */
    const res = response.data;
    // console.log(res);
    if (response.config.method === "delete") {
      // delete
      return res;
    }
    if (res.code !== 1000) {
      console.log('Server Error', res);

      // 401:;
      if (res.code === 401 || res.code === 403) {
        alert('...');
      }
      return Promise.reject("error");
    } else {
      // return res;
      return Promise.resolve(res);
    }
  });

  let reqObj = {url: api, method: method};
  if (['get', 'delete'].includes(method)) {
    // get delete
    reqObj = { ...reqObj, params: reqParams, };
  } else {
    // post put
    reqObj = { ...reqObj, data: reqObject, };
  }
  // console.log(reqObj);
  return service(reqObj);
}

export { sendAxiosRequest };
